.loginScreen{
    position: relative;
    height : 100vh;
    margin: 0;
    background : url('https://thedrum-media.imgix.net/thedrum-prod/s3/news/tmp/10557/netflixa.jpg?w=608&ar=default&fit=crop&crop=faces,edges&auto=format&q=40&dpr=2') center no-repeat;
    background-size: cover;
}

.loginScreen__logo{
    position: fixed;
    left : 0;
    width: 150px;
    object-fit: contain;
    padding-left: 20px;
}

.loginScreen__gradient{
    width: 100%;
    height: 100vh;
    z-index: 1;
    /* overflow: hidden;
    margin: 0; */
    background: rgba(0, 0, 0, 0.4);
    background-image : linear-gradient(
        to top,
        rgba(0, 0, 0, 0.8) 0,
        rgba(0, 0, 0, 0) 60%,
        rgba(0, 0, 0, 0.8) 100%
    );
}

.loginScreen__body > h1{
    font-size: 2.125 rem;
    margin-bottom: 20px;
}

.loginScreen__body > h2{
    font-size: 1.3rem;
    font-weight: 400;
    margin-bottom: 30px;
}

.loginScreen__body > h3{
    font-size: 1rem;
    font-weight: 400;
}

.loginScreen__input > form > input{
    padding: 10px;
    outline-width: 0;
    height: 30px;
    width: 30%;
    border: none;
    max-width: 600px;
}

.loginScreen__input {
    margin: 20px;
}

.loginScreen__getStarted{
    padding: 16px 20px;
    font-size : 1rem;
    color: #fff;
    background-color: #e50914;
    border: none;
    font-weight: 600px;
    cursor: pointer;
}



.loginScreen__body {
    position: absolute;
    top: 10%;
    margin-left: auto;
    margin-right: auto;
    text-align: center;
    z-index: 1;
    color: #fff;
    padding: 20px;
    left: 0;
    right: 0;
    margin-bottom: 40%;
}

.loginScreen__button{
    position: fixed;
    right: 20px;
    top:20px;
    padding : 10px 20px;
    font-size: 1rem;
    color : #fff;
    background: #e50914;
    cursor: pointer;
    border: none;
    font-weight: 600;
}